@use '../base/variables' as v;

.testimonial {
  background-color: v.$verde;
  padding: 2rem;
  color: v.$blanco;
  font-size: 2.4rem;
  border-radius: 2rem;

    blockquote {
    padding-left: 5rem;
    position: relative;

    &::before {
      content: '';
      background-image: url(/build/img/comilla.svg);
      background-repeat: no-repeat;
      width: 6rem;
      height: 8rem;
      display: block;
      position: absolute;
      left: -2rem;
    }
  }

  p {
    text-align: right;
    color: v.$blanco;
  }
}

